import areaSelect from '@/components/area-select/area-select'
import wPicker from "@/components/w-picker/w-picker.vue"
import { mapActions } from 'vuex'

export default {
	components: {
		areaSelect,
		wPicker
	},
	data() {
		return {
			form: {
				contactor_name: '',
				phone: '',
				address_id: null,
				address: '',
				street: '',
				is_default: 0
			},
			result:{
				result:""
			},
		}
	},
	onLoad(option){
		let title = '新增收货地址';
		if(option.type==='edit'){
			title = '编辑收货地址'
			this.form = JSON.parse(option.data)
			console.log(this.form)
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
		this.getAreas()
	},
	methods: {
		...mapActions(['getUserAddress', 'getAreas']),
		switchChange(e){
			this.form.is_default = e.detail.value ? 1 : 0;
		},
		// changeAddress(msg) {
		// 	console.log(msg)
		// 	this.form.address = msg
		// },
		chooseAddress() {
			this.$refs.ref.region.show()
		},
		// onConfirm(data) {
		// 	console.log(data, 'one')
		// 	this.form.address = data.result
		// },
		onConfirm(res,type){
			this.result=res;
			this.form.address = res.result
			console.log(res)
			console.log(this.form.address, 'this.form.address')
		},
		showRegion() {
			this.$refs.region.show()
		},
		showPicker(type){
			this.$refs[type].show();
		},
		//提交
		confirm(){
			let data = this.form;
			if(!data.contactor_name){
				this.$api.msg('请填写收货人姓名');
				return;
			}
			if(!/(^1[3|4|5|6|7|8][0-9]{9}$)/.test(data.phone)){
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			console.log(data.address, "data.address")
			if(!data.address){
				this.$api.msg('请在选择所在位置');
				return;
			}
			// if(!data.address || !data.address_id){
			// 	this.$api.msg('请在选择所在位置');
			// 	return;
			// }
			if(!data.street){
				this.$api.msg('请填写街道信息');
				return;
			}
			this.$http.post('user.address.edit', this.form).then(res => {
				this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
				this.getUserAddress()
				setTimeout(() => {
					uni.navigateBack()
				}, 800)
			})
		},
	}
}
